.shape{
	fill-opacity: 0.5;
}
.scale{
	fill-opacity: 0;
	stroke: white;
	stroke-width: .7;
}
.axis{
	stroke: white;
	stroke-width: .7;
}
.dot{
	fill: #3399FF;
	stroke: #3399FF;
	r: 5;
	-webkit-transition: r .2s;
	-o-transition: r .2s;
	transition: r .2s;
}
.caption{
	fill: white;
	font-family: 'Titillium Web', sans-serif;
	font-weight: 900;
	font-size: calc((2vh + 2vw)/3);
}
